header {
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
  color: var(--white75);
  font-size: 30px;
  font-weight: 800;
  grid-area: header;
  grid-template-areas: 'nav-icon navigation';
  grid-template-columns: 1fr 1fr;
  height: 75px;
  padding: 1rem;
  text-shadow: 2px 5px var(--themeBGColorMain);

  nav {
    grid-area: navigation;
    display: flex;
    flex-direction: row;
    justify-self: end;
    font-size: 22px;
    font-weight: 500;

    a {
      padding: 0 1rem;
    }
  }

  // graphic to left of frontpage-title
  .nav-icon {
    grid-area: nav-icon;
    text-align: left;
  }

  // only for frontpage
  // grid-column: to center across vw ...otherwise the main title would only center to 25% of the total width;
  .frontpage-title {
    text-align: center;
    grid-column: span 2;
  }

  // for responsive menu
  .topnav .icon {
    display: none;
  }
}

/************** Responsive CSS *******/

/* When the screen is less than 600 pixels wide, hide all links. Show the link that contains should open and close the topnav (.icon) */
@media screen and (max-width: 794px) {
  header {
    .topnav {
      a {
        display: none;
      }

      .icon {
        float: right;
        display: block;
      }
    }
  }
}

/* The "responsive" class is added to the topnav with JavaScript when the user clicks on the icon. This class makes the topnav look good on small screens (display the links vertically instead of horizontally) */
@media screen and (max-width: 794px) {
  header {
    .topnav.responsive {
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 10;
      left: 0;
      top: 0;
      background: #333;
      display: grid;
      text-align: center;
      justify-content: center;

      a {
        display: block;
        text-align: left;
        align-self: center;
      }

      .icon {
        position: absolute;
        right: 0;
        display: block;
        // idea: move menu icon over the menu in responsive mode
        z-index: 1;
        margin: 17px 35px 0 0;
        outline: inherit;
        align-self: start;
      }
    }
  } // ends header
} // ends media Q
